
import { Routes } from '@angular/router';
import { AppShellComponent } from './shell/shell.component';
import { HomeComponent } from './shell/home/home.component';


export const ROUTES: Routes = [
    { path: '', component: AppShellComponent, children: [
        { path: 'home', component: HomeComponent },
        { path: 'book', loadChildren: './shell/booking/booking.module#AppBookingModule' },
        { path: 'explore', loadChildren: './shell/explore/explore.module#AppExploreModule' },
        { path: 'schedule', loadChildren: './shell/new-schedule/schedule.module#AppScheduleModule' },
        { path: '**',      redirectTo: 'home' },
    ] },
    { path: '**',      redirectTo: 'home' },
];
