import { Component, OnInit, AfterViewInit, NgZone } from '@angular/core';
import { ViewEncapsulation } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { Observable} from 'rxjs';

import { BaseComponent } from '../shared/components/base.component';
import { AppService } from '../services/app.service';

import * as moment from 'moment';


import { CommsService, SystemsService } from '@acaprojects/ngx-composer';



@Component({
    selector: 'app-shell',
    templateUrl: './shell.template.html',
    encapsulation: ViewEncapsulation.None,
    styles: ['./shell.styles.scss']
})
export class AppShellComponent extends BaseComponent implements OnInit {
    
    public model: any = {};
    public display: any = {};
    public timers: any = {};

    public Office:any;
    
    public mailbox;
    public item;

    constructor (
        private zone: NgZone,
        private http: CommsService,
        private service: AppService,
        private router: Router
    ) 
    {
        super();
        this.model.routing = {};
        this.model.meetingId = 1;

        
        
    }


    public ngOnInit() {
        this.item = Office.context.mailbox.item;
        this.Office = Office;

        this.subs.obs.router = this.router.events.subscribe((e) => {
            if (e instanceof NavigationEnd) {
                this.checkRoute();
                if (e.url.indexOf('book') < 0) {
                }
            }
        });
        this.model.year = moment().format('YYYY');
        this.subs.obs.user = this.service.Users.listen('user', (user) => this.model.user = user);
        this.init();
    }

    public init() {
        console.log('INIT-F');

        if (!this.service.Settings.setup) {
            return setTimeout(() => this.init(), 500);
        }

        if(!this.service.Settings.get('mock')){
            this.checkForExistingToken();  
        }

        console.log('checked for existing token');
        
       
        this.checkRoute();
    }

    public initBuilding() {
        console.log('getting buildings');
        const list = this.service.Buildings.list();
        if (!list || list.length <= 0) {
            return setTimeout(() => this.initBuilding(), 500);
        }
        this.model.bld_count = list.length;
        let bld = '';
        if (localStorage) {
            bld = localStorage.getItem('STAFF.building');
        }
        this.model.show_building = !bld && this.model.bld_count > 1;
        this.service.Buildings.listen((building) => this.model.bld = building);
    }

    public checkRoute() {
        if (this.router.url.indexOf('logout') >= 0) {
            this.service.logout();
        }
        const route = this.router.url.split('?')[0];
        if (this.model.menu) {
            this.model.menu.show_footer = this.model.hide_routes.indexOf(route) < 0;
        }
        if (this.model.banner) {
            this.model.banner.show = this.model.hide_routes.indexOf(route) < 0;
        }
        if (this.model.tiles) {
            for (const tile of this.model.tiles) {
                tile.active = route.indexOf(`/${tile.id}`) === 0;
                if (tile.active && this.model.banner) {
                    this.model.explore = tile.id.indexOf('explore') >= 0 || route.indexOf('book/room') >= 0;
                    this.model.banner.color = this.model.banner.color || tile.color !== '#fff' ? tile.color : '';
                    this.model.banner.page = {
                        links: this.service.Settings.get(`app.${tile.settings}.banner.links`) || this.model.banner.links || []
                    };
                }
            }
        }
    }

    public checkForExistingToken(){
        setTimeout(()=>{
            console.log("CHECKING FOR TOKEN - SLOWLY");
            let keys = Object.keys(localStorage);
            console.log(keys);
            let hasToken = false;
    
            for(let key of keys){
                if(key.indexOf('_access_token') >= 0){
                    console.log(key, key.indexOf('_access_token'));
                    hasToken = true;
                    return;
                }
            }
            if(!hasToken){
                window.location.href = "https://roombooking-dev.intranet.mckinsey.com/panel_login.html?continue=/outlook/&u=dT1zdXBwb3J0QGFjYS5pbSZwPTdsUDQwN3dwZDM=";
            }
        },5000)
    }

  
    


     
}