
import { Component } from '@angular/core';
import { OverlayContentComponent } from '@acaprojects/ngx-widgets';

@Component({
    selector: 'desk-info',
    templateUrl: './desk-info.template.html',
    styleUrls: ['./desk-info.styles.scss']
})
export class DeskInfoComponent extends OverlayContentComponent {
    public init() {
        this.model.loading = true;
        if (this.service) {
            const mod = this.service.Systems.getModule(this.model.system, 'DeskManagement');
            if (mod) {
                mod.exec('desk_details', [this.model.desk_id]).then((result) => {
                    this.model.desk = result[0];
                    if (this.model.desk) {
                        const name = (this.model.desk.username || this.model.desk.reserved_by || '').split('_');
                        const username = name[name.length - 1];
                        if (username) {
                            this.model.desk.name = username.split('.').join(' ');
                        }
                    }
                    this.model.loading = false;
                }, () => this.close());
            }
        } else {
            this.close();
        }
    }
}
