
import { Component, Input, Output, OnChanges, EventEmitter, OnInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';

import { BaseComponent } from '../base.component';
import { DynamicField } from './dynamic-field.class';
import { Subscription } from 'rxjs';

@Component({
    selector: 'dynamic-form',
    templateUrl: './dynamic-form.template.html',
    styleUrls: ['./dynamic-form.styles.scss']
})
export class DynamicFormComponent extends BaseComponent implements OnChanges, OnInit {
    @Input() public fields: DynamicField<any>[] = [];
    @Input() public validate: boolean;
    @Output() public form = new EventEmitter();
    @Output() public valid = new EventEmitter();

    public form_group: FormGroup;
    public changes: Subscription;

    public ngOnInit() {
        this.form_group = this.makeFormGroup();
        this.subChanges();
    }

    public ngOnChanges(changes: any) {
        if (changes.fields) {
            this.form_group = this.makeFormGroup();
            this.subChanges();
        }
        if (changes.validate) {
            this.checkValid(true);
        }
    }

    public makeFormGroup() {
        const group: any = {};

        const handleItem = item => {
            let validators = [];
            if (item.required) { validators.push(Validators.required); }
            if (item.validators && item.validators.length > 0) {
                validators = [...validators, ...item.validators];
            }
            if (item.children) {
                item.children.forEach(handleItem);
            }
            group[item.key] = validators && validators.length > 0 ? new FormControl(item.value || '', validators)
                : new FormControl(item.value || '');
            if (item.dirty) { group[item.key].setDirty(); }
        };

        if (this.fields && this.fields instanceof Array) {
            this.fields.forEach(handleItem);
        }
        return new FormGroup(group);
    }

    public subChanges() {
        if (this.changes) {
            this.changes.unsubscribe();
            this.changes = null;
        }
        this.changes = this.form_group.valueChanges.subscribe(() => this.checkValid());
    }

    public checkValid(mark: boolean = false) {
        let valid = true;
        if (this.form_group) {
            Object.keys(this.form_group.controls).forEach(key => {
                const item = this.form_group.controls[key];
                if (item.invalid) { valid = false; }
                if (mark) { item.markAsTouched(); }
            });
        }
        if (valid) { this.emitForm(); }
        this.valid.emit(valid);
    }

    public emitForm() {
        const form_data: any = {};
        if (this.form_group) {
            Object.keys(this.form_group.controls).forEach(key => {
                const item = this.form_group.controls[key];
                form_data[key] = item.value;
            });
        }
        this.form.emit(form_data);
    }
}
