import { Injectable } from '@angular/core';
import { CommsService } from '@acaprojects/ngx-composer';
import { BehaviorSubject } from 'rxjs';

import { BaseService } from './base.service';
import { Utils } from '../../shared/utility.class';

import * as moment from 'moment';

export interface IZoomMeeting {
    host: string;
    start: number;
    duration: number;
    topic?:string;
    agenda?: string;
    password?:string;
    alternative_host?:string;
    
}

export interface IZoomMeetingRequest {
    email: string;
    start: number;
    end: number;
    topic?:string;
    agenda?: string;
    password?:string;
    alternative_host?:string;
    instant: boolean;
}


@Injectable({
    providedIn: 'root'
})
export class ZoomService extends BaseService {

    constructor(protected http: CommsService) {
        super();
        this.model.name = 'zoom';
        this.model.route = '/zoom';
        this.subjects.list = new BehaviorSubject<IZoomMeeting[]>([]);
        this.observers.list = this.subjects.list.asObservable();
    }

    /**
     * Get Zoom details
     * @param email email fo current user
     */
    public getId(email: string){
        const key = `getkey|${email}`;
        if (!this.promises[key]) {
            this.promises[key] = new Promise((resolve, reject) => {
                const url = `${this.endpoint}/?email=${email}`;
                this.http.get(url).subscribe(
                    (resp:any) => {
                        this.promises[key] = null;
                        resolve(resp);
                    },
                    (err) => {
                        this.promises[key] = null;
                        reject(err);
                    }
                );
            });
        }
        return this.promises[key];
    }

    /**
     * Register Meeting
     * @param options //email of user
     */
    public register(options: IZoomMeetingRequest){        
        const key = `register|${options.email}`;
        if (!this.promises[key]) {
            console.log('hitting zoom api');
            this.promises[key] = new Promise((resolve, reject) => {
                const url = `${this.endpoint}`;
                const time = moment(options.start).valueOf();
                const duration = moment(options.end).diff(moment(options.start), 'minutes');
                console.log()
                this.http.post(url, {
                    email:options.email,
                    start: time,
                    duration: duration,
                    topic: options.topic,
                    agenda: options.agenda,
                    password: options.password,
                    alternative_host: options.alternative_host,
                    instant: options.instant
                }).subscribe(
                    (resp: any) => {
                        console.log('MEETING RETURN: ', resp);
                        // const resp = this.processItem(resp);
                        resolve(resp);
                        this.promises[key] = null;
                    }, (err) => {
                        this.promises[key] = null;
                        reject(err);
                    }
                );
            });
        }
        return this.promises[key];
    }

    protected processItem(raw_item: any, id?: string) {
        const item: IZoomMeeting = {
            host: raw_item.host,
            start: raw_item.start,
            duration: raw_item.duration,
            topic:raw_item.topic,
            agenda: raw_item.agenda,
            password:raw_item.password,
            alternative_host:raw_item.alternative_host
        };
        return item;
    }
}
